*{
  box-sizing: border-box;
  }

body{
  margin: 0px;
  height: 100vh;
  background-color: bisque;
}

/* Header component css*/
nav{
  display: flex;
  align-items: center; 
  background-color: black;
  padding: 30px, 25px;
  height: 90px;
}

.nav-icon-text, .nav-title-text{
  margin: 0px;
}

.nav--icon{
  height: 30px;
  margin-right:  7px;
}

.nav-icon-text{
  margin-right: auto;
  color: #61dafb;
  font-weight: 700;
  font-size: 22px;
}

.nav-title-text{
  color: whitesmoke;
  font-weight: 600;
  margin-right: 20px;
}

/* Main component css*/
main{
  padding: 55px 25px;
  color: black;
  background-image:url(images/react-bg-logo.png);
  background-repeat: no-repeat;
  background-position-x:right;
  background-position-y:75% ;

}

.main--title{
  margin: 0%;
  font-size: 40px;
  letter-spacing: -0.05em;
}

.main-fact{
  margin-top: 45px ;
  max-width: 400px;
}

.main-fact>li{
  padding-block:10px;
}

.main-fact>li::marker{
  color: rgb(30, 42, 56);
  font-size: 1.4rem;
}

.footer-para{
  
  display: inline;
  }

